.InfoSettings{
  background-color: #3498db;
  background: -webkit-gradient(linear, left top, right bottom, from(#3498db), to(#2c3e50));
  background: -webkit-linear-gradient(top left, #3498db, #2c3e50);
  background: -o-linear-gradient(top left, #3498db, #2c3e50);
  background: linear-gradient(to bottom right, #3498db, #2c3e50);
}

.highlight{
  border-color: red;
}

button{
  border: 1px solid black;
  font-size: 2em;
  border-radius: 5%;
  margin-bottom: 10px;
  cursor:pointer;
}



